import * as React from 'react';
import styled from 'styled-components';

export interface ListProps {
  start: number;
  'data-level': number;
  'data-istasklist': boolean;
  style: React.CSSProperties;
}

const ulLevelMap = ['disc', 'circle', 'square'];
export const Ul = styled.ul<ListProps>`
  position: relative;
  margin-bottom: 0;
  margin-top: 0;
  list-style-type: ${props => (
    props['data-istasklist']
      ? 'none'
      : ulLevelMap[props['data-level'] % ulLevelMap.length]
  )};
`;

const olLevelMap = ['decimal', 'lower-alpha', 'lower-roman'];
export const Ol = styled.ol<ListProps>`
  margin-bottom: 0;
  margin-top: 0;
  list-style-type: ${props => (
    props['data-istasklist']
      ? 'none'
      : olLevelMap[props['data-level'] % ulLevelMap.length]
  )};
`;

export const Li = styled.li`
  list-style: inherit;
`;
